    --panel-color: rgb(207, 236, 247);
    /* rgb(236, 232, 232); */
    --button-color: rgb(179, 223, 238);
    --button-hover-color: rgb(139, 218, 245);
    --button-active-color: rgb(103, 190, 219);
    --border-color: rgba(49, 111, 138, 0.548);
    --panel-border: 3px solid var(--border-color);
    /* rgb(185, 181, 181); */
    --tasks-container-color: rgb(228, 247, 253);
    /* whitesmoke; */
}

@font-face {
    font-family: 'Roboto-Mono Flex';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype-variations');
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype') tech('variations');
    font-weight: 100 1000;
}

@font-face {
    font-family: 'LexendDeca Flex';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('truetype-variations');
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('truetype') tech('variations');
    font-weight: 100 1000;
}

html {
    box-sizing: border-box;
    font-family: 'Roboto-Mono Flex';
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    height: 100svh;
    display: flex;
    flex-direction: column;
}

h1 {
    margin: 0px;
    padding: 1.5rem 2rem;
    background-color: var(--panel-color);
    border-bottom: var(--panel-border);
    font-family: 'LexendDeca Flex';
    font-weight: 600;
    font-size: 3rem;
}

main {
    height: 10%;    /*small height is compensated by flex: 1 that takes the rest of available space*/
    flex: 1;
    display: flex;

    .taskGroups {
        display: flex;
        flex-direction: column;
        width: clamp(320px, 30%, 450px);
        padding: 1.5rem 2rem;
        overflow-y: auto;
        background-color: var(--panel-color);
        border-right: var(--panel-border);

        h2 {
            font-size: 2rem;
            font-weight: 600;
            margin: 0px 0px 1.5rem;
            font-family: 'LexendDeca Flex';
        }

        ul {
            padding: 0px;
            margin: 0px;
            list-style-type: none;
        }

        li {
            display: flex;
            justify-content: center;
            margin-top: 6px;
            border-radius: 8px;

            >button:first-of-type {
                flex: 1;
                border-radius: 8px 0px 0px 8px;
                text-align: start;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }

            button+button {
                padding: 0px 0.35rem;
                display: flex;
                align-items: center;
                border-radius: 0px 8px 8px 0px;
            }
        }

        .showAll {
            text-align: start;
        }

        .addNewList {
            display: flex;
            align-items: center;
            margin-top: 12px;
            padding: 0.5rem 1.5rem;
            align-self: center;
            background-color: var(--panel-color);
        }

        li:hover,
        .showAll:hover,
        .addNewList:hover {
            box-shadow: 0px 0px 6px 2px rgb(88, 85, 85);
        }

        button {
            font-size: 1.5rem;
            padding: 0.5rem 1.5rem;
            border-radius: 8px;
            appearance: none;
            border: none;
            background-color: var(--button-color);
        }

        button:enabled:hover,
        button:focus-visible {
            z-index: 1;
            background-color: var(--button-hover-color);
            outline: 2px solid black;
        }

        button:focus-visible {
            outline-width: 4px;
        }

        button:enabled:active {
            background-color: var(--button-active-color);
        }
    }
}`, "",{"version":3,"sources":["webpack://./src/css/base.css"],"names":[],"mappings":"AAAA;IACI,iCAAiC;IACjC,wBAAwB;IACxB,kCAAkC;IAClC,wCAAwC;IACxC,yCAAyC;IACzC,yCAAyC;IACzC,6CAA6C;IAC7C,wBAAwB;IACxB,2CAA2C;IAC3C,gBAAgB;AACpB;;AAEA;IACI,+BAA+B;IAC/B,0EAAqF;IACrF,kFAA6F;IAC7F,qBAAqB;AACzB;;AAEA;IACI,8BAA8B;IAC9B,0EAAqF;IACrF,kFAA6F;IAC7F,qBAAqB;AACzB;;AAEA;IACI,sBAAsB;IACtB,+BAA+B;AACnC;;AAEA;;;IAGI,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,WAAW;IACX,oBAAoB;IACpB,oCAAoC;IACpC,kCAAkC;IAClC,8BAA8B;IAC9B,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,WAAW,KAAK,gFAAgF;IAChG,OAAO;IACP,aAAa;;IAEb;QACI,aAAa;QACb,sBAAsB;QACtB,+BAA+B;QAC/B,oBAAoB;QACpB,gBAAgB;QAChB,oCAAoC;QACpC,iCAAiC;;QAEjC;YACI,eAAe;YACf,gBAAgB;YAChB,sBAAsB;YACtB,8BAA8B;QAClC;;QAEA;YACI,YAAY;YACZ,WAAW;YACX,qBAAqB;QACzB;;QAEA;YACI,aAAa;YACb,uBAAuB;YACvB,eAAe;YACf,kBAAkB;;YAElB;gBACI,OAAO;gBACP,8BAA8B;gBAC9B,iBAAiB;gBACjB,mBAAmB;gBACnB,gBAAgB;gBAChB,uBAAuB;YAC3B;;YAEA;gBACI,oBAAoB;gBACpB,aAAa;gBACb,mBAAmB;gBACnB,8BAA8B;YAClC;QACJ;;QAEA;YACI,iBAAiB;QACrB;;QAEA;YACI,aAAa;YACb,mBAAmB;YACnB,gBAAgB;YAChB,sBAAsB;YACtB,kBAAkB;YAClB,oCAAoC;QACxC;;QAEA;;;YAGI,2CAA2C;QAC/C;;QAEA;YACI,iBAAiB;YACjB,sBAAsB;YACtB,kBAAkB;YAClB,gBAAgB;YAChB,YAAY;YACZ,qCAAqC;QACzC;;QAEA;;YAEI,UAAU;YACV,2CAA2C;YAC3C,wBAAwB;QAC5B;;QAEA;YACI,kBAAkB;QACtB;;QAEA;YACI,4CAA4C;QAChD;IACJ;AACJ","sourcesContent":[":root {\n    --panel-color: rgb(207, 236, 247);\n    /* rgb(236, 232, 232); */\n    --button-color: rgb(179, 223, 238);\n    --button-hover-color: rgb(139, 218, 245);\n    --button-active-color: rgb(103, 190, 219);\n    --border-color: rgba(49, 111, 138, 0.548);\n    --panel-border: 3px solid var(--border-color);\n    /* rgb(185, 181, 181); */\n    --tasks-container-color: rgb(228, 247, 253);\n    /* whitesmoke; */\n}\n\n@font-face {\n    font-family: 'Roboto-Mono Flex';\n    src: url('./../fonts/RobotoMono-VariableFont_wght.ttf') format('truetype-variations');\n    src: url('./../fonts/RobotoMono-VariableFont_wght.ttf') format(