___CSS_LOADER_EXPORT___.push([module.id, `dialog {
    position: fixed;
    padding: 2rem;
    border-radius: 24px;
    background-color: azure;

    p {
        margin-top: 0px;
        font-size: 2rem;
        font-weight: 700;
    }

    input,
    textArea,
    select {
        font-size: 1.25rem;
        border: 1px solid black;

        &:hover {
            outline: 2px solid black;
        }

        &:focus-visible {
            outline: 3px solid black;
        }
    }

    form div {
        display: grid;
        grid-template-columns: min-content 1fr;
        gap: 2rem;
        margin-bottom: 1.25rem;

        label {
            text-wrap: nowrap;
            font-size: 1.5rem;
        }
    }

    .descrContainer {
        grid-template-columns: none;
        grid-template-rows: min-content;
        gap: 0.75rem;
    }

    button {
        border: 1px solid black;
        border-radius: 8px;
        background-color: var(--button-color);
        font-size: 1.5rem;

        &:hover,
        &:focus-visible {
            background-color: var(--button-hover-color);
            outline: 2px solid black;
        }

        &:focus-visible {
            outline-width: 3px;
        }

        &:enabled:active {
            background-color: var(--button-active-color);
        }
    }

    .submitBtns {
        grid-template-columns: 1fr 1fr;
        margin: 2rem 0px 0px;

        button {
            padding: 0.25rem 1rem;
        }
    }

    [aria-label="close"] {
        appearance: none;
        position: absolute;
        right: 2rem;
        
        height: 35px;
        width: 35px;
    }
}`, "",{"version":3,"sources":["webpack://./src/css/dialog.css"],"names":[],"mappings":"AAAA;IACI,eAAe;IACf,aAAa;IACb,mBAAmB;IACnB,uBAAuB;;IAEvB;QACI,eAAe;QACf,eAAe;QACf,gBAAgB;IACpB;;IAEA;;;QAGI,kBAAkB;QAClB,uBAAuB;;QAEvB;YACI,wBAAwB;QAC5B;;QAEA;YACI,wBAAwB;QAC5B;IACJ;;IAEA;QACI,aAAa;QACb,sCAAsC;QACtC,SAAS;QACT,sBAAsB;;QAEtB;YACI,iBAAiB;YACjB,iBAAiB;QACrB;IACJ;;IAEA;QACI,2BAA2B;QAC3B,+BAA+B;QAC/B,YAAY;IAChB;;IAEA;QACI,uBAAuB;QACvB,kBAAkB;QAClB,qCAAqC;QACrC,iBAAiB;;QAEjB;;YAEI,2CAA2C;YAC3C,wBAAwB;QAC5B;;QAEA;YACI,kBAAkB;QACtB;;QAEA;YACI,4CAA4C;QAChD;IACJ;;IAEA;QACI,8BAA8B;QAC9B,oBAAoB;;QAEpB;YACI,qBAAqB;QACzB;IACJ;;IAEA;QACI,gBAAgB;QAChB,kBAAkB;QAClB,WAAW;;QAEX,YAAY;QACZ,WAAW;IACf;AACJ","sourcesContent":["dialog {\n    position: fixed;\n    padding: 2rem;\n    border-radius: 24px;\n    background-color: azure;\n\n    p {\n        margin-top: 0px;\n        font-size: 2rem;\n        font-weight: 700;\n    }\n\n    input,\n    textArea,\n    select {\n        font-size: 1.25rem;\n        border: 1px solid black;\n\n        &:hover {\n            outline: 2px solid black;\n        }\n\n        &:focus-visible {\n            outline: 3px solid black;\n        }\n    }\n\n    form div {\n        display: grid;\n        grid-template-columns: min-content 1fr;\n        gap: 2rem;\n        margin-bottom: 1.25rem;\n\n        label {\n            text-wrap: nowrap;\n            font-size: 1.5rem;\n        }\n    }\n\n    .descrContainer {\n        grid-template-columns: none;\n        grid-template-rows: min-content;\n        gap: 0.75rem;\n    }\n\n    button {\n        border: 1px solid black;\n        border-radius: 8px;\n        background-color: var(--button-color);\n        font-size: 1.5rem;\n\n        &:hover,\n        &:focus-visible {\n            background-color: var(--button-hover-color);\n            outline: 2px solid black;\n        }\n\n        &:focus-visible {\n            outline-width: 3px;\n        }\n\n        &:enabled:active {\n            background-c