___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --medium-priority-card-color: rgb(253, 228, 118);
    --medium-priority-hover-color: rgb(255, 219, 59);
    --medium-priority-active-color: rgb(223, 188, 35);
    --high-priority-card-color: rgb(255, 154, 123);
    --high-priority-hover-color: rgb(255, 116, 74);
    --high-priority-active-color: rgb(219, 86, 46);
}

.taskDisplay {
    flex: 1;
    background-color: var(--tasks-container-color);
    font-size: 1.5rem;
    padding: 1.5rem;
    overflow-y: auto;

    &:empty {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 16px;

        &::before {
            height: 100px;
            width: 100px;
            background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
            background-size: 100px 100px;
            content: "";
        }
        &::after {
            content: 'Wow, such empty';
            font-size: 2rem;
        }
    }

    h3 {
        font-size: 2rem;
        margin: 0px;
    }

    .allTasks {
        margin-top: 1.5rem;
        display: grid;
        grid-template-columns: repeat(auto-fit, 320px);
        gap: 1.5rem;
    }

    .addNewTask,
    .taskCard {
        padding: 1rem;
        border: 1px solid var(--border-color);
        border-radius: 12px;
        background-color: var(--button-color);
    }

    .addNewTask,
    .taskCard {
        &:hover,
        &:focus-visible {
            background-color: var(--button-hover-color);
            outline: 2px solid black;
        }

        &:focus-visible {
            outline-width: 4px;
        }

        &:active {
            background-color: var(--button-active-color);
        }
    }

    .addNewTask {
        height: 231px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .taskCard {
        p {
            margin: 0px;
        }

        .name {
            text-align: start;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        .descr {
            height: 110px;
            text-align: justify;
            display: -webkit-box;
            -webkit-line-clamp: 4;
            -webkit-box-orient: vertical;
            overflow: hidden;
        }

        .date {
            padding: 0px;
            text-align: end;
            border: none;
        }
    }

    .taskCard.medium {
        background-color: var(--medium-priority-card-color);

        &:hover,
        &:focus-visible {
            background-color: var(--medium-priority-hover-color);
        }

        &:active {
            background-color: var(--medium-priority-active-color);
        }
    }

    .taskCard.high {
        background-color: var(--high-priority-card-color);

        &:hover,
        &:focus-visible {
            background-color: var(--high-priority-hover-color);
        }

        &:active {
            background-color: var(--high-priority-active-color);
        }
    }

    .divider {
        height: 1px;
        margin: 0.5rem 0px;
        border-bottom: 1px solid var(--border-color);
    }

    & >.divider {
        margin: 1.5rem 0px;
    }
}`, "",{"version":3,"sources":["webpack://./src/css/rightPanel.css"],"names":[],"mappings":"AAAA;IACI,gDAAgD;IAChD,gDAAgD;IAChD,iDAAiD;IACjD,8CAA8C;IAC9C,8CAA8C;IAC9C,8CAA8C;AAClD;;AAEA;IACI,OAAO;IACP,8CAA8C;IAC9C,iBAAiB;IACjB,eAAe;IACf,gBAAgB;;IAEhB;QACI,aAAa;QACb,sBAAsB;QACtB,uBAAuB;QACvB,mBAAmB;QACnB,SAAS;;QAET;YACI,aAAa;YACb,YAAY;YACZ,yDAAqD;YACrD,4BAA4B;YAC5B,WAAW;QACf;QACA;YACI,0BAA0B;YAC1B,eAAe;QACnB;IACJ;;IAEA;QACI,eAAe;QACf,WAAW;IACf;;IAEA;QACI,kBAAkB;QAClB,aAAa;QACb,8CAA8C;QAC9C,WAAW;IACf;;IAEA;;QAEI,aAAa;QACb,qCAAqC;QACrC,mBAAmB;QACnB,qCAAqC;IACzC;;IAEA;;QAEI;;YAEI,2CAA2C;YAC3C,wBAAwB;QAC5B;;QAEA;YACI,kBAAkB;QACtB;;QAEA;YACI,4CAA4C;QAChD;IACJ;;IAEA;QACI,aAAa;QACb,aAAa;QACb,uBAAuB;QACvB,mBAAmB;IACvB;;IAEA;QACI;YACI,WAAW;QACf;;QAEA;YACI,iBAAiB;YACjB,mBAAmB;YACnB,gBAAgB;YAChB,uBAAuB;QAC3B;;QAEA;YACI,aAAa;YACb,mBAAmB;YACnB,oBAAoB;YACpB,qBAAqB;YACrB,4BAA4B;YAC5B,gBAAgB;QACpB;;QAEA;YACI,YAAY;YACZ,eAAe;YACf,YAAY;QAChB;IACJ;;IAEA;QACI,mDAAmD;;QAEnD;;YAEI,oDAAoD;QACxD;;QAEA;YACI,qDAAqD;QACzD;IACJ;;IAEA;QACI,iDAAiD;;QAEjD;;YAEI,kDAAkD;QACtD;;QAEA;YACI,mDAAmD;QACvD;IACJ;;IAEA;QACI,WAAW;QACX,kBAAkB;QAClB,4CAA4C;IAChD;;IAEA;QACI,kBAAkB;IACtB;AACJ","sourcesContent":[":root {\n    --medium-priority-card-color: rgb(253, 228, 118);\n    --medium-priority-hover-color: rgb(255, 219, 59);\n    --medium-priority-active-color: rgb(223, 188, 35);\n    --high-priority-card-color: rgb(255, 154, 123);\n    --high-priority-hover-color: rgb(255, 116, 74);\n    --high-priority-active-color: rgb(219, 86, 46);\n}\n\n.taskDisplay {\n    flex: 1;\n    background-color: var(--tasks-container-color);\n    font-size: 1.5rem;\n    padding: 1.5rem;\n    overflow-y: auto;\n\n    &:empty {\n        display: flex;\n        flex-direction: column;\n        justify-content: center;\n        align-items: center;\n        gap: 16px;\n\n        &::before {\n            height: 100px;\n            width: 100px;\n            background-image: url(../icons/ldoge-svgrepo-com.svg);\n            background